import {FactoryProvider, InjectionToken} from '@angular/core';
import {IRepository} from '../../../../domain/interfaces/irepository.interface';


import {SignatureService} from './signature.service';
import {SignatureRequest} from '../../../../domain/models/plans/SignatureRequest';

export const SignatureToken = new InjectionToken<IRepository<SignatureRequest>>('signature.service');

export class SignatureProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<SignatureRequest>> = SignatureToken;
  public deps: any[] = [SignatureService];
  public multi = false;
  // tslint:disable-next-line:ban-types
  public useFactory: Function = (...deps: IRepository<SignatureRequest>[]) => {
    return deps[0];
  }
}
