import { Observable } from 'rxjs';
import { RootObject } from '../../../../domain/models/register/rootobject.model';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IRepository } from '../../../../domain/interfaces/irepository.interface';

import {UrlPath} from '../../../../domain/enums/urlpath';
import {SignatureRequest} from '../../../../domain/models/plans/SignatureRequest';

@Injectable()
export class SignatureService implements IRepository<SignatureRequest> {

  constructor(private http: HttpClient) {
  }

  public create(signatureRequest: SignatureRequest): Observable<RootObject> {
    // tslint:disable-next-line:prefer-const max-line-length
    let response = this.http.get<RootObject>(UrlPath.URL_PATH + '/servicebroker/v1/private/plans/pc/signature/' + signatureRequest.value + '/');
    return response;
  }
}
