import {Component, Inject, OnInit, ViewChild} from '@angular/core';
import {CommunicationService} from '../../../services/Communication.service';
import {Example} from '../../../../../domain/models/example/RootObject';
import {IRepository} from '../../../../../domain/interfaces/irepository.interface';
import {ProfileRequest} from '../../../../../domain/models/profile/profilerequest.model';
import {PcPlansToken} from '../../../services/plans/pcplans.provider';
import {BoPCPlan} from '../../../../../domain/models/plans/bopcplan.model';
import {Router} from '@angular/router';
import {PayPlanToken} from '../../../services/plans/payplan.provider';
import {CuponRequestToken} from '../../../services/plans/cuponrequest.provider';
import {RequestPlan} from '../../../../../domain/models/plans/requestplan.model';
import {CuponRequest} from '../../../../../domain/models/plans/cuponrequest.model';
import {CuponResponse} from '../../../../../domain/models/plans/cuponresponse.model';
import {PayPlanModel} from '../../../../../domain/models/plans/payplan.model';
import {UserSession} from '../../../../../domain/models/profile/usersession.model';
import {NgForm} from '@angular/forms';
import {PayUModel} from '../../../../../domain/models/plans/PayUModel';
import {SignatureToken} from '../../../services/plans/signature.provider';
import {SignatureRequest} from '../../../../../domain/models/plans/SignatureRequest';

@Component({
  selector: 'app-sumaryplan',
  templateUrl: './sumaryplan.component.html',
  styleUrls: ['./sumaryplan.component.scss']
})
export class SumaryPlanComponent implements OnInit {

  public listBoPCPlan: BoPCPlan[] = [];
  public subTotal = 0.0;
  public iva = 0.0;
  public totalToPay = 0.0;
  public cuponCode: string;
  public cuponCodeVisible = 0;
  public cuponResponse: CuponResponse;
  public messageCupon: string;
  public payModel = new PayPlanModel();
  public userSession = new UserSession();
  public vEmail: string;
  public pagoOnline = new PayUModel();
  public urlpayu = 'https://sandbox.checkout.payulatam.com/ppp-web-gateway-payu/';

  constructor(
    @Inject(PcPlansToken) private boPCPlanIRepository: IRepository<BoPCPlan>,
    @Inject(CuponRequestToken) private cuponRepository: IRepository<CuponRequest>,
    @Inject(PayPlanToken) private payPlanToken: IRepository<PayPlanModel>,
    private router: Router) {
    if (this.router.getCurrentNavigation() != null &&
      this.router.getCurrentNavigation().extras != null &&
      this.router.getCurrentNavigation().extras.state != null &&
      this.router.getCurrentNavigation().extras.state.listBoPCPlan != null) {
      this.listBoPCPlan = this.router.getCurrentNavigation().extras.state.listBoPCPlan;
      this.vEmail = localStorage.getItem('currentUser');
      this.calculateTotal();

    } else {
      this.router.navigate(['products/pcplanselection']);
    }
  }

  private calculateTotal() {
    this.subTotal = 0;
    this.totalToPay = 0;
    this.listBoPCPlan.forEach(item => {
      this.subTotal = this.subTotal + (item.count * item.price);
    });

    //El iva no aplica para SAN ANDRES
    this.iva = (this.subTotal * 19) / 100;
    this.totalToPay = this.iva + this.subTotal;
  }

  selectOtherPlan() {
    this.router.navigate(['products/pcplanselection']);
  }

  ngOnInit() {

    this.userSession = JSON.parse(sessionStorage.getItem('userSession'));
  }

  cuponShowHide() {
    this.messageCupon = '';
    // tslint:disable-next-line:triple-equals
    if (this.cuponCodeVisible == 0) {
      this.cuponCodeVisible = 1;
    } else {
      this.cuponCodeVisible = 0;
    }
    this.cuponCode = '';
  }

  validateCupon() {
    this.calculateTotal();
    // tslint:disable-next-line:prefer-const
    let request = new CuponRequest();
    request.cupon = this.cuponCode;
    request.idThirdPublic = this.userSession.idThirdPublic;

    this.cuponRepository.create(request).subscribe(response => {
      this.cuponResponse = response.content;
      if (this.cuponResponse.discount > 0) {
        this.messageCupon = 'Código: ' + request.cupon + ' ' + this.cuponResponse.discount + '% descuento';
        // tslint:disable-next-line:prefer-const
        let discountCupon = (this.totalToPay * this.cuponResponse.discount) / 100;
        this.totalToPay = this.totalToPay - discountCupon;
      } else {
        this.messageCupon = 'Código: ' + request.cupon + ' no tiene descuento';
      }
      // console.log(this.cuponResponse);
    });
  }

  applyCupon() {

  }


  loadPlans() {

  }


  post(obj, url) {
    console.log(obj);
    const mapForm = document.createElement('form');
    //mapForm.target = '_blank';
    mapForm.method = 'POST'; // or "post" if appropriate
    mapForm.action = url;
    // tslint:disable-next-line:only-arrow-functions
    Object.keys(obj).forEach(function(param) {
      // tslint:disable-next-line:prefer-const
      let mapInput = document.createElement('input');
      mapInput.type = 'hidden';
      mapInput.name = param;
      mapInput.setAttribute('value', obj[param]);
      mapForm.appendChild(mapInput);
    });
    document.body.appendChild(mapForm);
    mapForm.submit();
  }

  redirectToPayU(signature: string, idTransactionPublicReferenceCode: string) {


    this.pagoOnline.merchantId = '508029';
    this.pagoOnline.accountId = '512321';
    this.pagoOnline.description = 'Pago de planes Presta Clic';
    this.pagoOnline.referenceCode = idTransactionPublicReferenceCode;
    this.pagoOnline.amount = this.totalToPay;
    this.pagoOnline.tax = this.iva;
    this.pagoOnline.taxReturnBase = this.subTotal;
    this.pagoOnline.currency = 'COP';
    this.pagoOnline.signature = signature;
    this.pagoOnline.test = '1';
    this.pagoOnline.buyerEmail = 'bpalaciosa@test.com';
    this.pagoOnline.responseUrl = 'http://www.test.com/response';
    this.pagoOnline.confirmationUrl = 'http://www.test.com/confirmation';

    this.post(this.pagoOnline, this.urlpayu);


  }

  payPage() {

    this.payModel.payPlan = this.listBoPCPlan;
    this.payModel.cuponRequest = this.cuponCode;
    this.payModel.idThirdPublic = this.userSession.idThirdPublic;
    this.payModel.idUserPublic = this.userSession.idUserPublic;
    this.payModel.totalToPay = this.totalToPay;
    this.payPlanToken.create(this.payModel).subscribe(response => {
      let payPlanModel = new PayPlanModel();
      payPlanModel = response.content;
      this.redirectToPayU(payPlanModel.signature, payPlanModel.idTransactionPublicReferenceCode);
    });

  }


}
